/** @format */

import React, { useEffect, useState } from 'react';
import axios from '../../../axios';
import useDebug from '../../../hooks/useDebug';
import useTitle from '../../../hooks/useTitle';
import Page from '../../../Types/Page';
import Publication from '../../../Types/Publication';
import ReactImage from '../../Image';

export const papaimg =
	'data:image/jpeg;base64,';
const calculateDispayTime = (dateString: string) => {
	const givenDate = new Date(dateString);
	const currentDate = new Date();
	if (givenDate.getFullYear() !== currentDate.getFullYear()) return 'Before this year';
	if (givenDate.getMonth() !== currentDate.getMonth()) return 'Earlier this year';
	if (currentDate.getDate() - givenDate.getDate() > 7) return 'Earlier this month';
	if (currentDate.getDate() - givenDate.getDate() > 1) return 'Earlier this Week';
	if (currentDate.getHours() - givenDate.getHours() >= 1) return 'Earlier today';
	return Math.floor((currentDate.getTime() - givenDate.getTime()) / (1000 * 60)) + 'mins ago';
};
export const Home: React.FC = props => {
	const [Data, setData] = useState<Page<Publication> | null>(null);
	useTitle('Home');
	useDebug('Home', props);
	useEffect(() => {
		axios.get('/api/public/users/').then(res => {
			setData(res.data);
			console.log(res.data);
		});
	}, []);
	return (
		<>
			<Header />
			<Body {...{ Data }} />
		</>
	);
};

const Header = () => {
	return (
		<section className='bg-dark p-5  text-light text-center'>
			<div className='container d-flex justify-content-between'>
				<div>
					<h1 className='fw-light'>Welcome to AlokMeds</h1>
					<p className='lead'> Get all your doubts clear and also view my latest publications</p>
				</div>
				<div>
					<ReactImage className='img-fluid' src={papaimg} alt='' />
				</div>
			</div>
		</section>
	);
};

const Body: React.FC<{ Data: null | Page<Publication> }> = ({ Data }) => {
	return (
		<div className='my-4 container flex-row d-flex'>
			{Data?.content?.map(publication => (
				<div key={publication.link} className='card bg-light mx-5'>
					<div className='card-body text-center py- overflow-hidden'>
						<ReactImage height={400} width={400} className='img-fluid rounded-circle' src={papaimg} alt='' />
						<h4 className='card-text text-dark'>{publication.description}</h4>
						<div className='d-flex justify-content-between align-items-center'>
							<div className='btn-group'>
								<a href={publication.link} target='_blank' rel='noreferrer' className='btn btn-sm btn-outline-secondary'>
									View
								</a>
							</div>
							<small className='text-muted overflow-hidden'>{calculateDispayTime(publication.createdDateTime.toDateString())}</small>
						</div>
					</div>
				</div>
			))}
		</div>
	);
};

export default Home;
